import React, { useState, useEffect } from 'react';
import useWebSocket/*, { ReadyState }*/ from 'react-use-websocket';
import { 
    useAppDispatch, 
    updateGasStats,
    updateGasCounts,
    updateEthPrice
} from '../redux';


function parseMessageData(data: string) {
    try {
        data = JSON.parse(data);
    } catch (err) {
        console.error('WS Data Parse Error:');
        console.log(err);
    }
    return data;
};

function logSocketError(err: any) {
    console.error(err);
}


export function WebsocketProvider() {
    const dispatch = useAppDispatch();
    const [socketUrl, ] = useState('wss://api.stab.io/ws/');

    const {
        // sendMessage,
        lastMessage,
        // readyState,
    } = useWebSocket(socketUrl);
    
    // const connectionStatus = {
    //     [ReadyState.CONNECTING]: 'Connecting',
    //     [ReadyState.OPEN]: 'Open',
    //     [ReadyState.CLOSING]: 'Closing',
    //     [ReadyState.CLOSED]: 'Closed',
    //     [ReadyState.UNINSTANTIATED]: 'Uninstantiated',
    // }[readyState];

    // useEffect(() => {
    //     // console.log(readyState);
    //     // if (readyState === 1) {
    //     //     sendMessage('auth');
    //     // }
    // }, [readyState]);

    useEffect(() => {
        let message: any = {};
        let data: any = {};
        if (lastMessage !== null && lastMessage.isTrusted && lastMessage.data) {
            try {
                message = JSON.parse(lastMessage.data);
            } catch (err) {
                console.error('WS Message Parse Error:');
                console.log(err);
            }

            if (message.d && message.t) {
                data = parseMessageData(message.d);

                if (message.t === 'gc') {
                    dispatch(updateGasCounts(data.map((d: any) => { return {gasPrice: d.g, count: d.c}})));
                } else if (message.t === 'gs') {
                    dispatch(updateGasStats(data));
                } else if (message.t === 'ep') {
                    dispatch(updateEthPrice(data));
                } else {
                    logSocketError(`Invalid WS Message Type: ${lastMessage}`);
                }

                // console.log(data);
            } else {
                logSocketError(`Invalid WS Message Type or Data: ${lastMessage}`);
            }
            
            // find out what type of message
        } else if (lastMessage !== null) {
            logSocketError(`Invalid WS Message: ${lastMessage}`);
        }

    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [lastMessage]);


    // const handleClickSendMessage = useCallback(() =>
    //     sendMessage('Hello'), []);

    return <></>;
}